import React from 'react'
import { Switch,Route,HashRouter } from 'react-router-dom'

import Components from '../components'

const router:React.FC = ()=>{
    return <HashRouter>
    <Switch>
            <Route path="/Login">
                    <Components.Login/>
            </Route>
            <Route path="/Home">
                    <Components.Home/>
            </Route>
            <Route path="/Accessories">
                    <Components.Accessories/>
            </Route>
            <Route path="/CarInfo">
                    <Components.CarInfo/>
            </Route>
            <Route path="/CarMaintenAppoint">
                    <Components.CarMaintenAppoint/>
            </Route>
            <Route path="/CustomerInfo">
                    <Components.CustomerInfo/>
            </Route>
            <Route path="/MaintenDetail">
                    <Components.MaintenDetail/>
            </Route>
            <Route path="/ReturnInfo">
                    <Components.ReturnInfo/>
            </Route>
            <Route path="/StaffInfo">
                    <Components.StaffInfo/>
            </Route>
    </Switch>
</HashRouter>
}
    

export default router